import React, { useState } from "react";
import axios from "axios";
import Header from "./Header";
import { useNavigate } from "react-router-dom";
import "../styles/Login.css";

const Login = () => {
  const navigate = useNavigate();

  const [memberId, setMemberId] = useState("");
  const [password, setPassword] = useState("");
  const [errors, setErrors] = useState({});
  const [loginError, setLoginError] = useState(null);

  //   
  const validate = () => {
    const newErrors = {};

    // memberId  
    if (!memberId) {
      newErrors.memberId = " .";
    } else if (!/^[a-z0-9]{8,30}$/.test(memberId)) {
      newErrors.memberId =
        " 8~30      .";
    }

    // password  
    if (!password) {
      newErrors.password = " .";
    } else if (password.length < 8 || password.length > 255) {
      newErrors.password = " 8  255   .";
    } else if (/\s/.test(password)) {
      newErrors.password = "    .";
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  // memberId   
  const handleMemberIdChange = (e) => {
    const lowercaseValue = e.target.value.toLowerCase();
    setMemberId(lowercaseValue);
  };

  //   
  const handleSubmit = async (event) => {
    event.preventDefault();
    if (!validate()) return;

    try {
      const response = await axios.post("/api/members/login", {
        memberId,
        password,
      });
      localStorage.setItem("token", response.data.token); //  
      navigate("/mypage"); //    
    } catch (error) {
      setLoginError(" .   .");
    }
  };

  return (
    <div className="login-container">
      <header>
        <Header />
      </header>
      <main>
        <h2></h2>
        <form onSubmit={handleSubmit}>
          <div className="form-group">
            <input
              type="text"
              value={memberId}
              onChange={handleMemberIdChange}
              autoComplete="off"
              placeholder=""
            />
            {errors.memberId && (
              <p className="error-message">{errors.memberId}</p>
            )}
          </div>
          <div className="form-group">
            <input
              type="password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              autoComplete="off"
              placeholder=""
            />
            {errors.password && (
              <p className="error-message">{errors.password}</p>
            )}
          </div>
          {loginError && <p className="error-message">{loginError}</p>}
          <button className="login-button" type="submit"></button>
        </form>
      </main>
      <footer>
        <p>GATHER MIND ?</p>
        <p>
          <span onClick={() => navigate("/signup")}></span> 
            !
        </p>
      </footer>
    </div>
  );
};

export default Login;
