import React, { useState, useEffect } from "react";
import axios from "axios";
import Header from "./Header";
import { useNavigate } from "react-router-dom";
import "../styles/EditProfile.css";

const EditProfile = () => {
  const [memberInfo, setMemberInfo] = useState({
    memberId: " ",
    email: " ",
    nickname: "",
  });
  const [newNickname, setNewNickname] = useState("");
  const [newPassword, setNewPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const [errors, setErrors] = useState({});
  const [formError, setFormError] = useState("");
  const [isNicknameUnique, setIsNicknameUnique] = useState(null);
  const navigate = useNavigate();

  useEffect(() => {
    const fetchCurrentUserInfo = async () => {
      try {
        const token = localStorage.getItem("token");
        const response = await axios.get("/api/members/me", {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        });
        setMemberInfo({
          memberId: response.data.memberId || " ",
          email: response.data.email || " ",
          nickname: response.data.nickname || " ",
        });
      } catch (error) {
        console.error("     :", error);
      }
    };
    fetchCurrentUserInfo();
  }, []);

  const validateField = async (field, value) => {
    const newErrors = { ...errors };

    if (!value && field === "password" && newPassword) {
      newErrors.password = "  .";
    } else if (
      field === "nickname" &&
      (value.length < 2 || value.length > 20)
    ) {
      newErrors.nickname = " 2 20  .";
    } else if (
      field === "password" &&
      newPassword &&
      (value.length < 8 || value.length > 255)
    ) {
      newErrors.password = " 8  255   .";
    } else if (field === "password" && /\s/.test(value)) {
      newErrors.password = "    .";
    } else {
      if (field === "nickname" && value !== memberInfo.nickname) {
        try {
          const response = await axios.post(`/api/members/check-nickname`, {
            nickname: value,
          });
          if (!response.data.isUnique) {
            newErrors.nickname = "   .";
            setIsNicknameUnique(false);
          } else {
            setIsNicknameUnique(true);
            delete newErrors.nickname;
          }
        } catch (error) {
          console.error("   :", error);
        }
      } else {
        delete newErrors[field];
      }
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const validate = async () => {
    const newErrors = {};
    let isFormValid = true;

    //   
    if (newNickname && newNickname !== memberInfo.nickname) {
      const isNicknameValid = await validateField("nickname", newNickname);
      isFormValid = isFormValid && isNicknameValid;
    }

    //   
    if (newPassword) {
      const isPasswordValid = await validateField("password", newPassword);
      isFormValid = isFormValid && isPasswordValid;
    }

    //    
    if (confirmPassword && confirmPassword !== newPassword) {
      newErrors.confirmPassword = "  .";
      isFormValid = false;
    } else {
      delete newErrors.confirmPassword;
    }

    setErrors((prev) => ({ ...prev, ...newErrors }));
    return isFormValid;
  };

  const handleNicknameCheck = async () => {
    await validateField("nickname", newNickname);
  };

  const handleSubmit = async (event) => {
    event.preventDefault();

    if (!(await validate())) return;

    const originalNickname = memberInfo.nickname;
    let successMessage = "";

    if (!newNickname && !newPassword) {
      alert("  .");
      return navigate("/mypage");
    }

    try {
      if (newNickname && newNickname !== originalNickname && isNicknameUnique) {
        await axios.put(
          "/api/members/update",
          { nickname: newNickname },
          {
            headers: {
              Authorization: `Bearer ${localStorage.getItem("token")}`,
            },
          }
        );
        successMessage += `${originalNickname}  ${newNickname} .\n`;
      }

      if (newPassword) {
        await axios.put(
          "/api/members/update",
          { password: newPassword },
          {
            headers: {
              Authorization: `Bearer ${localStorage.getItem("token")}`,
            },
          }
        );
        successMessage += `${
          newNickname || originalNickname
        }   .\n`;
      }

      alert(successMessage.trim());
      navigate("/mypage");
    } catch (error) {
      setFormError("  .  .");
    }
  };

  const handleDeleteAccount = async (event) => {
    event.preventDefault();
    const confirmDelete = window.confirm("   ?");
    if (confirmDelete) {
      try {
        const token = localStorage.getItem("token");
        await axios.delete("/api/members/delete-account", {
          headers: { Authorization: `Bearer ${token}` },
        });
        alert("  .");
        localStorage.removeItem("token");
        navigate("/goodbye");
      } catch (error) {
        alert("  .");
      }
    } else {
      navigate("/serious");
    }
  };

  return (
    <div className="edit-profile-container">
      <header>
        <Header />
      </header>
      <main>
        <h2> </h2>
        <form onSubmit={handleSubmit} autoComplete="off">
          <div className="form-group">
            <strong>:</strong> {memberInfo.memberId}
          </div>
          <div className="form-group">
            <strong>:</strong> {memberInfo.email}
          </div>

          <div className="form-group">
            <input
              type="text"
              value={newNickname}
              onChange={(e) => {
                setNewNickname(e.target.value);
                setIsNicknameUnique(null);
              }}
              autoComplete="off"
              placeholder=" "
            />
            <button
              type="button"
              onClick={handleNicknameCheck}
              className="check-button"
            >
              
            </button>
            {isNicknameUnique === true && (
              <p className="success-message">  .</p>
            )}
            {errors.nickname && (
              <p className="error-message">{errors.nickname}</p>
            )}
          </div>

          <div className="form-group">
            <input
              type="password"
              value={newPassword}
              onChange={(e) => setNewPassword(e.target.value)}
              autoComplete="off"
              placeholder=" "
            />
            {errors.password && (
              <p className="error-message">{errors.password}</p>
            )}
          </div>
          <div className="form-group">
            <input
              type="password"
              value={confirmPassword}
              onChange={(e) => setConfirmPassword(e.target.value)}
              autoComplete="off"
              placeholder="  "
            />
            {errors.confirmPassword && (
              <p className="error-message">{errors.confirmPassword}</p>
            )}
          </div>

          {formError && <p className="error-message">{formError}</p>}
          <button className="mypage-edit-button" type="submit">
            
          </button>
          <button
            className="mypage-delete-button"
            type="button"
            onClick={handleDeleteAccount}
          >
            
          </button>
        </form>
      </main>
    </div>
  );
};

export default EditProfile;
