import React, { useEffect, useState } from "react";
import axios from "axios";
import { useNavigate } from "react-router-dom";
import Header from "./Header";
import "../styles/Mypage.css";

const Mypage = () => {
  const [memberInfo, setMemberInfo] = useState({
    memberId: " ",
    nickname: " ",
    email: " ",
  });
  const navigate = useNavigate();

  useEffect(() => {
    const fetchMemberInfo = async () => {
      const token = localStorage.getItem("token"); //   
      if (!token) {
        alert(" .");
        navigate("/login");
        return;
      }

      try {
        const response = await axios.get("/api/members/me", {
          headers: { Authorization: `Bearer ${token}` }, //   
        });
        setMemberInfo(response.data || {});
      } catch (error) {
        console.error("     .", error);
        alert(" .");
        navigate("/login");
      }
    };

    fetchMemberInfo();
  }, [navigate]);

  const handleEditInfo = () => {
    navigate("/editprofile"); //    
  };

  return (
    <div className="mypage-container">
      <header>
        <Header />
        <h2>{memberInfo.nickname}  </h2>
        <ul className="mypage-nav">
          <li onClick={() => navigate("/mypage")}> </li>
          <li onClick={() => navigate("/mypage/joined-study")}>
             
          </li>
          <li onClick={() => navigate("/mypage/written-question")}>
             
          </li>
          <li onClick={() => navigate("/mypage/written-answer")}>
             
          </li>
        </ul>
      </header>

      {/*    */}
      <div className="mypage-info-box">
        <p className="mypage-info">
          <h3></h3> <span>{memberInfo.memberId || "Undefined"}</span>
        </p>
        <p className="mypage-info">
          <h3></h3> <span>{memberInfo.nickname || "Undefined"}</span>
        </p>
        <p className="mypage-info">
          <h3></h3> <span>{memberInfo.email || "Undefined"}</span>
        </p>
      </div>
      <button onClick={handleEditInfo}> </button>
    </div>
  );
};

export default Mypage;
