import React, { useState } from "react";
import { dateFormat } from "../services/QuestionService";
import axios from "axios";
import { useNavigate } from "react-router-dom";

const Answer = ({answer, fetch, scrollToRef}) => {

    const navigate = useNavigate();

    const [isEditing, setIsEditing] = useState(false);
    const [editedContent, setEditedContent] = useState(answer.content);

    const handleEdit = () => {
        setIsEditing(!isEditing);
    }

    const handleSave = async () => {
        if (editedContent === "") {
            alert(" .");
            return;
        }
        try {
            const response = await axios.put(`http://localhost:8080/api/answer/${answer.answerId}`,
                editedContent, 
                {
                    headers: { "Content-Type": "text/plain" } // text/plain 
                }
            );
            console.log("  ", response);
            setIsEditing(false);
            setTimeout(()=>{fetch()}, 500);
            setTimeout(()=>{scrollToRef()}, 800);
        } catch (error) {
            console.log("  ", error);
        }
    };

    const handleDelete = async () => {
        if (window.confirm(" ?")) {
            await axios.delete(`http://localhost:8080/api/answer/${answer.answerId}`);
            setTimeout(()=>{fetch()}, 500);
            alert(" .");
        }
    }

    return (
        <div>
            <p className="answer-nickname">{answer.nickname}</p>
            {isEditing ? (
                //  
                <div className="edit-mode">
                    <input
                        type="text"
                        value={editedContent}
                        onChange={(e) => setEditedContent(e.target.value)}
                        className="edit-input"
                    />
                    <button onClick={handleSave} className="save-button"></button>
                    <button onClick={handleEdit} className="cancel-button"></button>
                </div>
            ) : (
                //    
                <>
                    <div className="answer-content">{answer.content}</div>
                    <div className="answer-meta">
                        <p className="createdAt">{dateFormat(answer.createdAt)}</p>
                        {/*    */}
                        {answer?.memberId === "ID" && (
                            <div className="action-buttons">
                                <button className="edit-button" onClick={handleEdit}></button>
                                <button className="delete-button" onClick={handleDelete}></button>
                            </div>
                        )}
                        <div className="action-buttons">
                            <button className="edit-button" onClick={handleEdit}></button>
                            <button className="delete-button" onClick={handleDelete}></button>
                        </div>
                    </div>
                </>
            )}
        </div>
    );
};

export default Answer;