import axios from "axios";
import React, { useState } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import DaumPostcode from "react-daum-postcode";
import { subDays } from "date-fns";
import "../styles/MeetingForm.css";
import "../styles/CreateSchedule.css";

const CreateSchedule = () => {
  const navigate = useNavigate();
  // const {studyId} = useLocation(); // StudyInfo  StudyId  

  // StudyInfo  StudyId  
  // 24.11.11 suhwan
  const LocationDom = useLocation();
  const { studyId } = LocationDom.state || {};

  const [title, setTitle] = useState("");
  const [dateTime, setDateTime] = useState(new Date());
  const [location, setLocation] = useState("");
  const [description, setDescription] = useState("");

  //   
  const [popup, setPopup] = useState(false);
  const handleInput = (data) => {
    let fullAddress = data.address; //  
    let extraAddress = ""; // 

    if (data.addressType === "R") {
      //    
      if (data.bname !== "") {
        extraAddress += data.bname;
      }
      if (data.buildingName !== "") {
        extraAddress +=
          extraAddress !== "" ? `, ${data.buildingName}` : data.buildingName;
      }
      fullAddress += extraAddress !== "" ? ` (${extraAddress})` : "";
    }
    console.log(data);
    console.log(fullAddress);
    console.log(data.zonecode);

    setLocation(fullAddress);
  };
  const handleComplete = (e) => {
    e.preventDefault();
    setPopup(!popup);
  };

  //  
  const handleSubmit = async (e) => {
    e.preventDefault();

    console.log(" ");
    console.log(
      "study_id : ",
      studyId,
      "title : ",
      title,
      ", time : ",
      dateTime,
      ", location : ",
      location
    );

    try {
      const response = await axios.post("http://localhost:8080/api/schedule", {
        studyId: 1, // test
        title,
        dateTime: new Date(new Date(dateTime).getTime() + 540 * 60 * 1000),
        location,
        description,
      });
      console.log("   ", response);
    } catch (error) {
      console.log("  ");
    }

    navigate("./"); //    
  };

  return (
    <div className="container">
      <h1>  </h1>
      <form onSubmit={handleSubmit} className="form-container">
        <div className="form-group">
          <label>  : </label>
          <input
            type="text"
            value={title}
            onChange={(e) => setTitle(e.target.value)}
            placeholder=" "
            required
          />
        </div>
        <div className="form-group">
          <label>/ : </label>
          <div>
            <DatePicker
              selected={dateTime}
              onChange={(date) => setDateTime(date)}
              minDate={subDays(new Date(), 0)}
              timeInputLabel="Time:"
              dateFormat="yyyy.MM.dd h:mm aa"
              showTimeInput
            />
          </div>
        </div>
        <div className="form-group">
          <label> : </label>
          <input
            type="text"
            value={location}
            onChange={(e) => setLocation(e.target.value)}
            placeholder=" "
            required
          />
          <button onClick={handleComplete}> </button>
          {popup && (
            <span>
              <DaumPostcode autoClose onComplete={handleInput} />
            </span>
          )}
        </div>
        <div className="form-group">
          <label>  :</label>
          <textarea
            value={description}
            onChange={(e) => setDescription(e.target.value)}
          />
        </div>
        <button type="submit"></button>
      </form>
    </div>
  );
};

export default CreateSchedule;
