import React, { useState } from "react";
import axios from "axios";
import Header from "./Header";
import { useNavigate } from "react-router-dom";
import "../styles/Signup.css";

const SignUp = () => {
  const [memberId, setMemberId] = useState("");
  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const [email, setEmail] = useState("");
  const [nickname, setNickname] = useState("");
  const [errors, setErrors] = useState({});
  const [successMessages, setSuccessMessages] = useState({});
  const [signUpSuccess, setSignUpSuccess] = useState(false);
  const navigate = useNavigate();

  const validateAndCheckUniqueness = async (field, value) => {
    const newErrors = {};
    const newSuccessMessages = {};

    if (!value) {
      newErrors[field] = `${field}()  .`;
    } else if (field === "memberId" && !/^[a-z0-9]{8,50}$/.test(value)) {
      newErrors.memberId =
        " 8~50     .";
    } else if (
      field === "nickname" &&
      (value.length < 2 || value.length > 20)
    ) {
      newErrors.nickname = " 2 20  .";
    } else {
      try {
        const response = await axios.post(`/api/members/check-${field}`, {
          [field]: value,
        });
        if (response.data.isUnique) {
          newSuccessMessages[field] = `   ${
            field === "memberId" ? "" : ""
          }.`;
          newErrors[field] = ""; //   
        } else {
          newErrors[field] = `   ${
            field === "memberId" ? "" : ""
          }.`;
          delete newSuccessMessages[field]; //   
        }
      } catch (error) {
        console.error(`${field}   :`, error);
      }
    }

    setErrors((prev) => ({ ...prev, ...newErrors }));
    setSuccessMessages((prev) => ({ ...prev, ...newSuccessMessages }));

    //       
    return !newErrors[field];
  };

  const validate = async () => {
    const isMemberIdValid = await validateAndCheckUniqueness(
      "memberId",
      memberId
    );
    const isEmailValid = await validateAndCheckUniqueness("email", email);
    const isNicknameValid = await validateAndCheckUniqueness(
      "nickname",
      nickname
    );
    const newErrors = {};

    if (!password) {
      newErrors.password = "  .";
    }
    
    if (!confirmPassword) {
      newErrors.confirmPassword = "   .";
    } else if (password !== confirmPassword) {
      newErrors.confirmPassword = "  .";
    }

    setErrors((prev) => ({ ...prev, ...newErrors }));

    return (
      isMemberIdValid &&
      isEmailValid &&
      isNicknameValid &&
      !newErrors.confirmPassword
    );
  };

  const handleSubmit = async (event) => {
    event.preventDefault();
    if (!(await validate())) return;

    try {
      await axios.post("/api/members/signup", {
        memberId,
        password,
        email,
        nickname,
      });
      setSignUpSuccess(true);
      setTimeout(() => navigate("/login"), 3000); // 3    
    } catch (error) {
      setErrors({ form: " .  ." });
    }
  };

  return (
    <div className="signup-container">
      <header>
        <Header />
      </header>
      <main>
        <h2></h2>
        {signUpSuccess ? (
          <p>  !  .</p>
        ) : (
          <form onSubmit={handleSubmit} autoComplete="off">
            <div className="form-group">
              <label htmlFor="id"></label>
              <input
                type="text"
                value={memberId}
                onChange={(e) => setMemberId(e.target.value.toLowerCase())}
                autoComplete="off"
                placeholder=""
                name="id"
                id="id"
                className="signup-input"
              />
              <button
                type="button"
                onClick={() => validateAndCheckUniqueness("memberId", memberId)}
                className="signup-check-button"
              >
                
              </button>
              {errors.memberId && (
                <p className="error-message">{errors.memberId}</p>
              )}
              {successMessages.memberId && (
                <p className="success-message">{successMessages.memberId}</p>
              )}
            </div>

            <div className="form-group">
              <label htmlFor="password"></label>
              <input
                type="password"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                autoComplete="off"
                placeholder=""
                name="password"
                id="password"
                className="signup-input"
              />
              {errors.password && (
                <p className="error-message">{errors.password}</p>
              )}
            </div>

            <div className="form-group">
              <label htmlFor="confirmPassword"> </label>
              <input
                type="password"
                value={confirmPassword}
                onChange={(e) => setConfirmPassword(e.target.value)}
                autoComplete="off"
                placeholder=" "
                name="confirmPassword"
                id="confirmPassword"
                className="signup-input"
              />
              {errors.confirmPassword && (
                <p className="error-message">{errors.confirmPassword}</p>
              )}
            </div>

            <div className="form-group">
              <label htmlFor="email"></label>
              <input
                type="email"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                autoComplete="off"
                placeholder=""
                name="email"
                id="email"
                className="signup-input"
              />
              {errors.email && <p className="error-message">{errors.email}</p>}
            </div>

            <div className="form-group">
              <label htmlFor="nickname"></label>
              <input
                type="text"
                value={nickname}
                onChange={(e) => setNickname(e.target.value)}
                autoComplete="off"
                placeholder=""
                name="nickname"
                id="nickname"
                className="signup-input"
              />
              <button
                type="button"
                onClick={() => validateAndCheckUniqueness("nickname", nickname)}
                className="signup-check-button"
              >
                
              </button>
              {errors.nickname && (
                <p className="error-message">{errors.nickname}</p>
              )}
              {successMessages.nickname && (
                <p className="success-message">{successMessages.nickname}</p>
              )}
            </div>

            {errors.form && <p className="error-message">{errors.form}</p>}
            <button className="signup-button" type="submit"></button>
          </form>
        )}
      </main>
    </div>
  );
};

export default SignUp;
