import axios from "axios";
import React, { useEffect, useState } from "react";
import { useLocation, useNavigate, useParams } from "react-router-dom";
import QuestionForm from "../components/QuestionForm";

const QuestionFormPage = ({ isModify }) => {
    const navigate = useNavigate();
    //   const { studyId } = useLocation(); // StudyInfo  StudyId  

    // StudyInfo  StudyId  
    // 24.11.11 suhwan
    const LocationDom = useLocation();
    const { studyId } = LocationDom.state || {};
    const {id} = useParams();

    const [questionInitData, setQuestionInitData] = useState(null);
    
    useEffect(() => {
        if (isModify) { //     
            const fetchQuestion = async () => {
                try {
                    const data = await axios.get(`http://localhost:8080/api/question/${id}`);
                    console.log("  ");
                    
                    // console.log(response);
                    setQuestionInitData(data.data);
                } catch (error) {
                    console.log("  ");
                }
            };
            fetchQuestion();
        }
    }, [isModify, id]);

    const handleCreateQuestion = async (questionData) => {
        console.log(questionData);
        try {
            var response = null;

            if (isModify) {
                response = await axios.put(`http://localhost:8080/api/question/${id}`, questionData); 
                console.log("   ", response);
                navigate(`/question-detail/${id}`);
            } else {
                // response = await axios.post(`http://localhost:8080/api/question?memberId=${userId}&studyId=${studyId}`, questionData);
                response = await axios.post(`http://localhost:8080/api/question?memberId=user1&studyId=1`, questionData); // userId, studyId  
                console.log("   ", response);
                navigate(''); //     
            }
            } catch (error) {
            console.log(` ${isModify ? "" : ""} `);
        }
    };

    return (
        <div className="container">
        <h1>{isModify ? " " : "  "}</h1>
        <QuestionForm
            onSubmit={handleCreateQuestion}
            studyId={studyId}
            question={questionInitData}
        />
        </div>
    );
};

export default QuestionFormPage;
